.jumbotron{
    background-color:black;
    display:flex;
    width:100%;
    color:black;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    font-size:18px;
    flex-direction: row;
    display:flex;
}
/* .row{
    justify-content: center;
    align-content: center;
    text-align: center;
} */
.container{
    margin: 0 auto;
    align-content: center;
}


.col-lg-6{
    justify-content: center;
    text-align: center;
}
#mylineup-header{
    width:80%;
    margin:0 auto;
    background-color: white;
    border: solid 2px;
    border-color: teal;
    text-align:center;
    

}
#lineup-row{
    align-content: space-between;
    margin:auto;
    flex-direction: row;
}
#all-bands-header{
    color:hotpink;
    text-emphasis: center;
    margin-bottom:5px;
    text-align: center;
}

#add-bands-header{
    color: lime;
    text-align: center;
}
.button{
    margin-left: 10px !important;
    
}

.btn-primary-outline{
    margin-left:5px !important;
    margin-right:5px !important;
}
.card-title{
    text-align: center;
}
.card-text{
    display: flex 1;
    flex-direction: column;
    height:80px;
    padding-top:2%;
    overflow-y: auto;
    text-align: center;
    border-top: hotpink solid 2px;
    /* border: black 2px solid; */
}

.card-img-overlay{
    height:50px;
    background-color:rgba(83, 83, 83, 0.9);
    width: 100%;
    color:hotpink;
    text-align: center;
    font-size:.8em;
    font-weight: 500;
    
    margin-top: 53%;
}
img{
    height:200px;
}

.card-footer{
    display:flex;
   justify-content: space-between !important;
   background-color:#343a40
}
.btn-primary-outline{
    color: #007bff;
    background-color: #343a40;
    border-color: #007bff;
    margin-left: 10px;
}
 

@media only screen and (max-width: 600px) {
    .jumbotron{
        width: 100% !important;
    }
    #mylineup-header{
        width: 100% !important;
    }
}